"use client";

import React, { useEffect, useRef, useState } from "react";
import Image from "next/image";
import { createPortal } from "react-dom";
import { ScrollArea, ScrollBar } from "@/components/ui/scroll-area";
import {
  FaAngleRight,
  FaChevronLeft,
  FaChevronRight,
  FaPlayCircle,
} from "react-icons/fa";
import { CiBoxList } from "react-icons/ci";

const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
const IMAGE_BASE = process.env.NEXT_PUBLIC_TMDB_IMAGE;

interface Movie {
  id: number;
  title: string;
  name?: string;
  backdrop_path: string | null;
  poster_path: string | null;
}

const CARD_WIDTH = 250 + 16;

const Gems = ({name}: {name: string}) => {
  const [movies, setMovies] = useState<Movie[]>([]);
  const [hoveredMovie, setHoveredMovie] = useState<Movie | null>(null);
  const [hoverRect, setHoverRect] = useState<DOMRect | null>(null);

  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    fetch(
      `https://api.themoviedb.org/3/trending/movie/week?api_key=${API_KEY}`
    )
      .then((res) => res.json())
      .then((data) => setMovies(data.results || []))
      .catch(console.error);
  }, []);

  const scrollLeft = (e: React.MouseEvent) => {
    e.stopPropagation();
    scrollRef.current?.scrollBy({
      left: -CARD_WIDTH,
      behavior: "smooth",
    });
  };

  const scrollRight = (e: React.MouseEvent) => {
    e.stopPropagation();
    scrollRef.current?.scrollBy({
      left: CARD_WIDTH,
      behavior: "smooth",
    });
  };

  return (
    <div className="pt-4 w-full relative">
      {/* TITLE */}
      <div className="px-6 sm:px-10 md:px-20 mb-4">
        <h1 className="text-2xl font-bold text-white flex items-center gap-2">
        {name} <FaAngleRight />
        </h1>
      </div>

      {/* CAROUSEL */}
      <div className="relative px-6 sm:px-10 md:px-20 group">
        {/* LEFT */}
        <button
          className="hidden md:flex absolute left-0 top-1/2 -translate-y-1/2 z-40
                     bg-black/60 hover:bg-black text-white p-2 rounded-full
                     opacity-0 group-hover:opacity-100 transition"
        >
          <FaChevronLeft size={22} onClick={e => scrollLeft(e)} />
        </button>

        {/* RIGHT */}
        <button
         
          className="hidden md:flex absolute right-0 top-1/2 -translate-y-1/2 z-40
                     bg-black/60 hover:bg-black text-white p-2 rounded-full
                     opacity-0 group-hover:opacity-100 transition"
        >
          <FaChevronRight size={22} onClick={e => scrollRight(e)} />
        </button>

        <ScrollArea className="w-full" >
          <div ref={scrollRef} className="flex gap-4 py-10">
            {movies.map((movie) => {
              const imagePath =
                IMAGE_BASE && (movie.backdrop_path || movie.poster_path)
                  ? `${IMAGE_BASE}${movie.backdrop_path || movie.poster_path}`
                  : "/placeholder.jpg";

              return (
                <div
                  key={movie.id}
                  className="w-[250px] flex-shrink-0"
                  onMouseEnter={(e) => {
                    setHoveredMovie(movie);
                    setHoverRect(e.currentTarget.getBoundingClientRect());
                  }}
                  onMouseLeave={() => setHoveredMovie(null)}
                >
                  {/* BASE CARD */}
                  <div className="relative h-[150px] rounded-lg overflow-hidden bg-black">
                    <Image
                      src={imagePath}
                      alt={movie.title}
                      fill
                      className="object-cover"
                    />
                  </div>
                </div>
              );
            })}
          </div>

          <ScrollBar orientation="horizontal" className="bg-gray-800 h-2 mt-2" />
        </ScrollArea>
      </div>

      {/* ðŸ”¥ NETFLIX STYLE HOVER PREVIEW (PORTAL) */}
      {hoveredMovie &&
        hoverRect &&
        createPortal(
          <div
            style={{
              top: hoverRect.top - 10,
              left: hoverRect.left,
              width: hoverRect.width,
            }}
            className="fixed z-[9999] bg-[#181818] rounded-lg shadow-2xl
                       transition-all duration-300 scale-120 pointer-events-none"
          >
            {/* IMAGE */}
            <div className="relative h-[150px] rounded-t-lg overflow-hidden">
              <Image
                src={`${IMAGE_BASE}${hoveredMovie.backdrop_path}`}
                alt={hoveredMovie.title}
                fill
                className="object-cover"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/80 to-transparent" />
            </div>

            {/* DETAILS */}
            <div className="p-4 text-white pointer-events-auto">
              <div className="flex gap-3 mb-2">
                <p className="text-white text-sm font-semibold">
                  {hoveredMovie.title}
                </p>
                <button className="bg-white text-black p-2 h-8 z-10 w-8 rounded-full">
                  <FaPlayCircle />
                </button>
                <button className="bg-gray-700 p-2 h-8 w-8 rounded-full">
                  <CiBoxList />
                </button>
              </div>

              <p className="text-green-500 text-sm font-semibold">
                98% Match
              </p>
              <p className="text-xs text-gray-300 mt-1">
                Action â€¢ Thriller
              </p>
            </div>
          </div>,
          document.body
        )}
    </div>
  );
};

export default Gems;















